import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import PubSub, { topics } from '../../lib/pubsub';
import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Dog from '../../components/dog';
import User from '../../components/user';
import WorkoutGraph from '../../components/workout/graph';

const initialState = {
    newTeam: {},
};

@observer
class TeamsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadAll(props = this.props) {
        await this.loadTeam(props);
        await this.loadWorkoutPlans(props);
    }

    async loadTeam(props = this.props) {
		const { teamStore, userStore } = props.stores;
        await teamStore.load(props.team, false, { addData: ['dogs', 'users'] });
        const { team } = teamStore;
        await userStore.getWorkoutSummary({ team: team.id });
    }

    loadWorkoutPlans = async (props = this.props) => {
		this.setState({ isLoading: true });
		const { year } = props;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);
		const { workoutPlanStore, teamStore } = props.stores;
        const { team } = teamStore;

		workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});
		workoutPlanStore.resetGraphPlanWeekDistanceCurrent();

		if (team.id) {
			await workoutPlanStore.load({ query: {
				team: team.id,
				seasonYear: seasonStart,
				seasonMonth: 6,
			} });
			const { workoutPlans } = workoutPlanStore;
			const workoutPlanCurrent = workoutPlans[0] || {};
			if (workoutPlanCurrent.id) {
				await workoutPlanStore.load(workoutPlanCurrent.id);
				workoutPlanStore.prepareWeekGraphs({ year });
			}
		}
		this.setState({ isLoading: false });
	}

	deleteTeam = async () => {
		const { teamStore } = this.props.stores;
		const { team } = teamStore;
        await teamStore.saveField(team.id, 'offline', 1);
		PubSub.publish(topics.STATUS_MESSAGE, {
			message: 'Teamet er slettet',
			type: 'success',
			icon: 'fas fa-check',
		});
        route(`/teams/list`, true);
	}

    back = () => {
        route(`/teams/`, true);
    }

	componentDidMount() {
        this.loadAll();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.dog !== this.props.dog) {
            this.loadAll(nextProps);
        }
    }

	render() {
		const { teamStore, userStore, workoutPlanStore } = this.props.stores;
        const { workoutPlan, workoutPlans } = workoutPlanStore;

		const { team, dogs, users } = teamStore;
        const {
            user: currentUser,
        } = userStore;
        const workoutSummary2019 = teamStore.findWorkoutSummary(team.id, 2019) || {};
        const workoutSummary2020 = teamStore.findWorkoutSummary(team.id, 2020) || {};
        const isTeamAdmin = team.user === currentUser.id;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between mb-2'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 25px; width: 40px;`}
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>&nbsp;</h5>
                    </div>
                    <div>
                        <a class='btn btn-sm btn-primary float-right'
                            href={`/teams/edit/${team.id}`}
                        >
                            <i class='fas fa-edit' /> <Text id='teams.edit'>Endre</Text>
                        </a>
                    </div>
                </div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<h5>{team.name}</h5>
                        {isTeamAdmin && <span class='badge badge-pill badge-primary'><Text id='teams.admin'>Admin</Text></span>}

                        <WorkoutGraph stores={this.props.stores} isLoading={false} key={`${workoutPlan.id}-${workoutPlans.length}`} />

						<div class='container-fluid mb-2'>
							<div class='row mt-2 border-top'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.elevation || 0, 0)} m
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.elevation || 0, 0)} m
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2020-21</small>
                                    <h3>
										{util.secToHms(workoutSummary2020.duration || 0)}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2019-20</small>
                                    <h3>
                                        {util.secToHms(workoutSummary2019.duration || 0)}
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2 border-bottom'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.speedAvg || 0, 1)} <Text id='teams.kmt'>km/t</Text>
                                    </h3>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class='offset-0 col-12 mt-4'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
                            <h5><Text id='teams.dogs' fields={{ total: dogs.length }}>Hundene - {dogs.length} stk</Text></h5>
                        </div>
						{dogs && dogs.map(dog => (
							<Dog stores={this.props.stores} dog={dog} />
						))}
                    </div>

                    <div class='offset-0 col-12 mt-4'>
                        <h5><Text id='teams.members' fields={{ total: users.length }}>Medlemmer - {users.length} stk</Text></h5>
                        {users && users.map(user => (
							<User stores={this.props.stores} user={user} team={team} />
						))}
                    </div>

                    <div class='offset-0 col-12 mt-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<a class='btn btn-primary mt-5 float-right'
								href={`/teams/edit/${team.id}`}
							>
								<i class='fas fa-edit' /> <Text id='teams.edit'>Endre</Text>
							</a>
							<a class='btn btn-secondary mt-5 float-left'
								href={`/teams/addmember/${team.id}`}
							>
								<i class='fas fa-user' /> <Text id='teams.add-new-member'>Legg til medlem</Text>
							</a>
						</div>
					</div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-5'>
						<button class='btn btn-sm btn-danger mt-5 float-right'
							onClick={this.deleteTeam}
						>
							<i class='fas fa-trash' /> <Text id='teams.delete'>Slett</Text> {team.name}
						</button>
					</div>

				</div>
			</div>
		);
	}
}

export default TeamsDetail;
