import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import PubSub, { topics } from '../../lib/pubsub';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Map from '../../components/map';
import ImageSlider from '../../components/imageSlider';
import Dog from '../../components/dog';
import Musher from '../../components/user/musher';

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		case 4:
			return 'border-info';
		default:
			return 'border-warning';
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		case 4:
			return 'fas fa-futbol';
		default:
			return 'fas fa-hiking';
	}
}

function getWorkoutType(workout) {
	switch (workout.type) {
		case 1:
			return 'Trening';
		case 2:
			return 'Konkurranse';
		case 3:
			return 'Tur';
		case 4:
			return workout.typeTitle;
		default:
			return 'Ikke registrert'
	}
}

const initialState = {
    newDog: {},
};

@observer
class WorkoutDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadWorkout() {
		const { workoutStore } = this.props.stores;
        workoutStore.load(this.props.workout);
    }

	showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
		const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

	deleteWorkout = async () => {
		const { workoutStore } = this.props.stores;
		const { workout } = workoutStore;
        await workoutStore.saveField(workout.id, 'offline', 1);
		PubSub.publish(topics.STATUS_MESSAGE, {
			message: 'Treningen er slettet',
			type: 'success',
			icon: 'fas fa-check',
		});
        route(`/workouts/list`, true);
	}

    back = () => {
        route(`/workouts/list`, true);
    }

    componentDidMount() {
        this.loadWorkout();
    }

	render() {
		const { workoutStore, userStore } = this.props.stores;
		const { workout } = workoutStore;
		const track = userStore.findTrack(workout.track);
		const equipment = userStore.findEquipment(workout.equipment);
		const dogs = userStore.findDogs(workout.dogs);
		const mushers = userStore.findMushers(workout.mushers);
		const intensity = userStore.findIntensity(workout.intensity) || {};

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between mb-2'>
                    <div>
                    	<button class='btn btn-link float-left pl-0 fixed-top'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 25px; width: 40px;`}
							onClick={this.back}
						>
	        	            <i class='fas fa-arrow-circle-left' />
    		            </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>&nbsp;</h5>
                    </div>
                    <div>
                        <a class='btn btn-sm btn-primary float-right'
							href={`/workouts/edit/${workout.id}`}
						>
							<i class='fas fa-edit' /> <Text id='workouts.edit'>Endre</Text>
						</a>
                    </div>
                </div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='d-flex mb-2'>
							<div class={`text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`}
								style={workout.image ? `background-image: url("${workout.image.s3SmallLink}");` : ''}
							>
								{!workout.image && <i class={`${getWorkoutIcon(workout)} text-muted mt-3`} style='font-size: 40px;' />}
							</div>
							<div class='flex-grow-1 pl-3'>
								<h5 class='my-0'>{workout.name}</h5>
								{workout.team && userStore.findTeam(workout.team)}<br />
								<small class='text-muted'>
									{util.isoDate(workout.date, false, false, false)}
								</small>
							</div>
						</div>

						{workout && workout.gpx && <div style='height: 500px;'>
                            <Map stores={this.props.stores} mapId={workout.id} geojson={workout.gpx.geoJSON} gpxInfo={workout.gpxInfo} />
                        </div>}
						{track && track.gpx && !(workout && workout.gpx) && <div style='height: 500px;'>
                            <Map stores={this.props.stores} mapId={track.id} geojson={track.gpx.geoJSON} gpxInfo={track.gpx.gpxInfo} />
                        </div>}

						<div class='container-fluid mb-2 px-0'>
                            <div class='row'>
                                <div class='col-12'>
                                    <div class='d-flex mx-0'>
                                        {workout.images && <ImageSlider images={workout.images} />}
                                    </div>    
                                </div>
                            </div>
						</div>

						<div class='container-fluid'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
									<small class='text-muted'><i class='fas fa-clock ml-2' /> <Text id='workouts.duration'>Vargihet</Text></small>
									<h1>
										{util.secToHms(workout.duration)}
									</h1>
									<span class={`badge badge-${intensity.class}`}>{intensity.name}</span>
								</div>
	                            <div class='col text-center'>
									<small class='text-muted'><i class='fas fa-clock ml-2' /> <Text id='workouts.rest'>Hvile</Text></small>
									<h1 class='text-muted'>
										{util.secToHms(workout.rest)}
									</h1>
								</div>
	                        </div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
									<small class='text-muted'><i class='fas fa-tachometer-alt ml-2' /> <Text id='workouts.avgspeed'>Gj.fart</Text></small>
									<h3>
										{util.format(workout.speedAvg, 1)} <Text id='workouts.kmt'>km/t</Text>
									</h3>
								</div>
								<div class='col text-center'>
									<small class='text-muted'><Text id='workouts.type'>Type</Text></small>
									<h3>
										{getWorkoutType(workout)}
									</h3>
								</div>
	                        </div>
							<div class='row mt-2'>
								<div class='col text-center'>
									<small class='text-muted'><i class='fas fa-tools' /> <Text id='workouts.equipment'>Utstyr</Text></small>
									<h3>
										{equipment}
									</h3>
								</div>
								<div class='col text-center'>
									<small class='text-muted'><i class='fas fa-dog ml-2' /> <Text id='workouts.dogs'>Hunder</Text></small>
									<h3>
										{workout.dogs && <span>{workout.dogs.length}</span>}
									</h3>
								</div>
							</div>
							<div class='row mt-2'>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-road ml-2' /> <Text id='workouts.distance'>Distanse</Text></small>
									<h3>
										{util.format(workout.distanceKm, 0)} km
									</h3>
								</div>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-mountain ml-2' /> <Text id='workouts.ascend'>Stigning</Text></small>
									<h3>
										{util.format(workout.elevation, 0)} m
									</h3>
								</div>
							</div>
							<div class='row mt-2'>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-weather ml-2' /> <Text id='workouts.weather'>Vær</Text></small>
									<h3>
										{workout.weather}
									</h3>
								</div>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-mountain ml-2' /> <Text id='workouts.temperature'>Temperatur</Text></small>
									<h3>
										{workout.temperature}°C
									</h3>
								</div>
							</div>
						</div>
					</div>

                    <div class='offset-0 col-12 mt-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5><Text id='workouts.all-dogs' fields={{ total: dogs.length }}>Hundene - {dogs.length} stk</Text></h5>
						</div>
						{dogs && dogs.map(dog => (
							<Dog stores={this.props.stores} dog={dog} />
						))}
					</div>

                    <div class='offset-0 col-12 mt-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5><Text id='workouts.all-mushers' fields={{ total: mushers.length }}>Handlere - {mushers.length} stk</Text></h5>
						</div>
						{mushers && mushers.map(musher => (
							<Musher stores={this.props.stores} musher={musher} />
						))}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-5'>
						<a class='btn btn-primary mt-5 float-right'
							href={`/workouts/edit/${workout.id}`}
						>
							<i class='fas fa-edit' /> <Text id='workouts.edit'>Endre</Text> {workout.name}
						</a>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-5'>
						<button class='btn btn-sm btn-danger mt-5 float-right'
							onClick={this.deleteWorkout}
						>
							<i class='fas fa-trash' /> <Text id='workouts.delete'>Slett</Text> {workout.name}
						</button>
					</div>

				</div>
			</div>
		);
	}
}

export default WorkoutDetail;
