import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

@observer
class Strava extends Component {
    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

    ignoreStravaWorkout = async (e) => {
        e.stopPropagation();
        const id = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { stravaActivityStore } = this.props.stores;
		await stravaActivityStore.ignoreWorkout(id);
        this.loadStravaActivities();
    }

    importStravaWorkout = async (e) => {
        e.stopPropagation();
        const id = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { stravaActivityStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await stravaActivityStore.importWorkout(id, currentTeam);
        this.loadStravaActivities();
        this.loadWorkouts();
    }

    async loadStravaActivities() {
		const { stravaActivityStore } = this.props.stores;
		stravaActivityStore.load({ query: { hideImported: 1 }});
	}

    loadWorkouts = async () => {
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await workoutStore.load({ query: { team: currentTeam } });
	}

    render() {
        const { showDate,  } = this.state;
        const { activity, showDate: forceShowDate } = this.props;
		const { userStore, workoutStore } = this.props.stores;
        const { currentTeam } = workoutStore;
        const { team } = userStore;

        const selectedTeam = userStore.findTeam(currentTeam || team.id);

        return (
            <div class='w-100 mb-3'>
                <div class='row'>

                    {/* <td>{util.secToHms(activity.moving_time)}</td> */}
                    {/* <td>{util.secToHms(activity.elapsed_time)}</td> */}

                    <div class='col-12 d-flex mb-0 position-relative'>
                        <div class={`text-center border rounded-circle imageRounded`} style={`background-image: url("./assets/strava-icon.png"); border-width: 3px !important;`}>
                            &nbsp;
                        </div>
                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                            <h5 class='my-0 mb-1' style='line-height: 1.0em;'>{activity.name}</h5>
                            {activity.type}<br />
                            <small class='text-muted'>
                                {activity.start_date && <small onClick={this.toggleDate}>
                                    {forceShowDate || showDate ? util.formatDate(activity.start_date, { locale: 'no-NB' }) : util.formatDistance(activity.start_date, new Date(), { locale: 'no-NB' })}
                                </small>}
                                <br />
                            </small>
                            <small>
                                <span class='text-muted'>
                                    <i class='fas fa-road ml-2' /> {util.format(activity.distance / 1000, 1)}km
                                    <i class='fas fa-mountain ml-2' /> {util.format(activity.total_elevation_gain, 0)}m
                                    <i class='fas fa-tachometer-alt ml-2' /> {util.format(activity.average_speed * 3.6, 1)}<Text id='strava.kmt'>km/t</Text>
                                    <i class='fas fa-stopwatch ml-2' /> {util.secToHms(activity.elapsed_time)}
                                </span>
                                <i>{activity.url}</i><br />
                            </small>
                            <small>
                                <button class='btn btn-sm btn-secondary' onClick={this.ignoreStravaWorkout} data-id={activity.id}>
                                    <i class='fas fa-user-slash' /> <Text id='strava.ignore'>Ignore workout</Text>
                                </button>

                                <span class='float-right text-center'>
                                    <button class='btn btn-sm btn-primary' onClick={this.importStravaWorkout} data-id={activity.id}>
                                        <i class='fas fa-file-import' /> <Text id='strava.import'>Import workout</Text>
                                    </button><br />
                                    <small class='text-'>{selectedTeam}</small>
                                </span>
                            </small>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

export default Strava;
