import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import Strava from '../../components/strava';

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		if (team === currentTeam) {
			workoutStore.setCurrentTeam(undefined);
		} else {
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, workoutStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = workoutStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-2'>
							<div class={`border rounded rounded-pill px-3 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								style='line-height: 0.9em; cursor: pointer; pointer-events: inherit;'
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									<i class='fas fa-users mr-2' />
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadWorkouts = async () => {
		this.setState({ isLoading: true });
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await workoutStore.load({ query: { team: currentTeam } });
		// await this.loadWorkoutSummary();
		this.setState({ isLoading: false });
	}

	loadDogs = async () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	}

	async loadTracks() {
		const { trackStore } = this.props.stores;
		await trackStore.load();
	}

	async loadWorkoutSummary() {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
        await userStore.getWorkoutSummary({ team: currentTeam });
    }

	async loadStravaActivities() {
		const { stravaActivityStore } = this.props.stores;
		stravaActivityStore.load({ query: { hideImported: 1 }});
	}

    componentDidMount() {
        this.loadWorkouts();
		this.loadDogs();
		this.loadTracks();
		this.loadStravaActivities();
    }

	render() {
		const { isLoading } = this.state;
		const { trackStore, workoutStore, dogStore, appState, stravaActivityStore, userStore } = this.props.stores;
		const { darkmode } = appState;
		const { message } = this.props;
		const { workouts } = workoutStore;
		const { tracks } = trackStore;
		const { dogs } = dogStore;
		const { user } = userStore;
		const { stravaActivities } = stravaActivityStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center'>
						<a href='/workouts/' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> <Text id='workout.index.dashboard'>Oversikt</Text></a>
					</div>
					<div class='col text-center border-bottom border-primary'>
						<a href='/workouts/list' class='text-primary stretched-link'><i class='fas fa-list' /> <Text id='workout.index.list'>Liste</Text></a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/plan' class='text-secondary stretched-link'><i class='fas fa-calendar-alt' /> <Text id='workout.index.plan'>Plan</Text></a>
					</div>
				</div>

				{message && (
					<div class='alert alert-success mb-3' role='alert'>
						<i class='fas fa-check' /> {message}
					</div>
				)}
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.loadWorkouts} />
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-2'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> <Text id='workout.index.add-workout'>Legg til treningstur</Text>
						</a>
					</div>

					{user && user.strava && stravaActivities && stravaActivities.length > 0 && (
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 border-bottom pt-5 pb-5 mb-5'>
							<h5><Text id='workout.index.strava-workouts'>Strava workouts / not imported</Text></h5>
							{stravaActivities && stravaActivities.map(act => <Strava stores={this.props.stores} activity={act} />)}
						</div>
					)}

					<div class='col-12'>

						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5>
								<Text id='workouts.all-workouts' fields={{ total: workouts.length }}>Treningsturene - {workouts.length} stk</Text>
							</h5>
						</div>

						{!isLoading && workouts && workouts.length === 0 && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-muted'>
								{dogs && dogs.length === 0 && (
									<div class='mb-5 py-3 text-center border-top border-bottom'>
										<div class='display-4'>
											<i class='fas fa-dog' />
										</div>
										<h5>
											<Text id='workouts.no-dogs'>Ingen hunder er registert.</Text>
										</h5>
										<Text id='workouts.no-dogs-tip'>Det er lurt å legge inn hunder før du registrerer en treningstur.</Text><br />
										<a class='btn btn-info mt-3' href='/dogs/edit/new'>
											<i class='fas fa-dog' /> <Text id='workouts.add-dog'>Legg til hund</Text>
										</a>
									</div>
								)}

								{tracks && tracks.length === 0 && (
									<div class='mb-5 py-3 text-center border-top border-bottom'>
										<div class='display-4'>
											<i class='fas fa-map-marked' />
										</div>
										<h5>
											<Text id='workouts.no-tracks'>Ingen runder er registert.</Text>
										</h5>
										<Text id='workouts.no-tracks-tip-1'>Det er lurt å legge inn runden før du registrerer en treningstur. Da får du automatisk fylt ut distanse og høydemeter.</Text><br />
										<Text id='workouts.no-tracks-tip-2'>Dersom du ikke har GPX-filen fra denne runden, så kan du likevel registrere treningen.</Text><br />

										<a class='btn btn-info mt-3' href='/tracks/edit/new'>
											<i class='fas fa-map-marked' /> <Text id='workouts.add-track'>Legg til runde</Text>
										</a>
									</div>
								)}

								<div class='text-center mb-5'>
									<div class='display-1'>
										<i class='fas fa-running' />
									</div>
									<h5>
										<Text id='workouts.no-workouts'>Ingen treningsturer er registrert ennå.</Text>
									</h5>
								</div>
								<p class='text-center'>
									<p>
										<Text id='workouts.no-workouts-1'>Tracking i appen er ikke helt klart ennå pga noen tekniske begrensninger i Apple iOS.</Text>
									</p>
									<p>
										<Text id='workouts.no-workouts-2'>Når jeg får tak i en Android telefon skal jeg får satt opp denne funksjonen slik at den kan brukes der.</Text>
									</p>
								</p>
								<p class='text-center'>
									<Text id='workouts.no-workouts-3'>Kart over ruten du har kjørt legges inn som GPX under runder.</Text>
								</p>
								<Text id='workouts.no-workouts-howto'>Slik kommer du i gang</Text>:
								<ol>
									<li><Text id='workouts.no-workouts-howto-1'>Trykk på knappen "Legg til treningstur" oppe til høyre.</Text></li>
								</ol>
							</div>
						)}

						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} showDate={true} />
						))}
					</div>
					{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<a class='btn btn-primary btn-sm mt-5 float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div> */}
				</div>
			</div>
		);
	}
}

export default Workouts;
